<template>
    <div id="view-patient">
       <ul class="collection with-header">
            <li class="collection-header"><h4>{{name}}</h4></li>
            <li class="collection-item">Patient ID#:{{patient_id}}</li>
            <li class="collection-item">Date of Birth: {{date_of_birth}}</li>
            <li class="collection-item">Age: {{age}}</li>
            <li class="collection-item">Phone: {{phone}}</li>
            <li class="collection-item">Sex: {{sex}}</li>   
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deletePatient" class="btn red">Delete</button>   
        
        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-patient', params: {patient_id: patient_id}}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-patient',
    data () {
        return {
            patient_id: null,
            name: null,
            date_of_birth: null,
            age: null,
            phone: null,
            sex: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('patients').where('patient_id', '==', to.params.patient_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.patient_id = doc.data().patient_id
                    vm.name = doc.data().name
                    vm.date_of_birth = doc.data().date_of_birth
                    vm.age = doc.data().age
                    vm.phone = doc.data().phone
                    vm.sex = doc.data().sex
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('patients').where('patient_id', '==', this.$route.params.patient_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.patient_id = doc.data().patient_id
                    this.name = doc.data().name
                    this.date_of_birth = doc.data().date_of_birth
                    this.age = doc.data().age
                    this.phone = doc.data().phone
                    this.sex = doc.data().sex
                })
            })
        },
        deletePatient () {
            if(confirm('Are you sure?')) {
                db.collection('patients').where('patient_id', '==', this.$route.params.patient_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.delete()
                    this.$router.push('/')
                })
            })
            }
        }
    }
}
</script>


