<template>
    <div id="new-patient">
        <h3>New Patient</h3>
        <div class="row">
            <form @submit.prevent="savePatient" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="patient_id" required>
                        <label>Patient ID#</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <label>Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="date_of_birth" required>
                        <label>Date of Birth</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="age" required>
                        <label>Age</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="phone" required>
                        <label>Phone</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="sex" required>
                        <label>Sex</label>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-patient',
    data () {
        return {
            patient_id: null,
            name: null,
            date_of_birth: null,
            age: null,
            phone: null,
            sex: null
        }
    },
    methods: {
        savePatient () {
            db.collection('patients').add({
                patient_id: this.patient_id,
                name: this.name,
                date_of_birth: this.date_of_birth,
                age: this.age,
                phone: this.phone,
                sex: this.sex
            })
            .then(docRef => this.$router.push('/'))
            .catch(error => console.log(err))
        }    
    }
}
</script>


