<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Patients</h4></li>
            <li v-for="patient in patients" v-bind:key="patient.id" class="collection-item">
                {{patient.patient_id}} : {{patient.name}}

                <router-link class="secondary-content " v-bind:to="{name: 'view-patient', params: {patient_id: patient.patient_id}}">
                   <i class="fa fa-eye"></i> 
                </router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data () {
        return {
            patients: []
        }
    },
    created() {
        db.collection('patients').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id' : doc.id,
                    'patient_id' : doc.data().patient_id,
                    'name' : doc.data().name,
                    'date_of_birth' : doc.data().date_of_birth,
                    'age' : doc.data().age,
                    'phone' : doc.data().phone,
                    'sex' : doc.data().sex
                }
                this.patients.push(data)
            })
        })
    }
}
</script>


