<template>
    <div id="edit-patient">
        <h3>Edit Patient</h3>
        <div class="row">
            <form @submit.prevent="updatePatient" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="patient_id" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="date_of_birth" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="age" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="phone" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="sex" required>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-patient',
    data () {
        return {
            patient_id: null,
            name: null,
            date_of_birth: null,
            age: null,
            phone: null,
            sex: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('patients').where('patient_id', '==', to.params.patient_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.patient_id = doc.data().patient_id
                    vm.name = doc.data().name
                    vm.date_of_birth = doc.data().date_of_birth
                    vm.age = doc.data().age
                    vm.phone = doc.data().phone
                    vm.sex = doc.data().sex
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('patients').where('patient_id', '==', this.$route.params.patient_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.patient_id = doc.data().patient_id
                    this.name = doc.data().name
                    this.date_of_birth = doc.data().date_of_birth
                    this.age = doc.data().age
                    this.phone = doc.data().phone
                    this.sex = doc.data().sex
                })
            })
        },
        updatePatient() {
            db.collection('patients').where('patient_id', '==', this.$route.params.patient_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        patient_id: this.patient_id,
                        name: this.name,
                        date_of_birth: this.date_of_birth,
                        age: this.age,
                        phone: this.phone,
                        sex: this.sex
                    })
                    .then(() => {
                        this.$router.push({name: 'view-patient', params: {patient_id: this.patient_id}})
                    })
                })
            })
        }
    }
}
</script>


